  margin-right: 0.5rem;
  outline: none;
  position: relative;
  z-index: 1000; }
  .option-input:hover {
    background: #fff; }
  .option-input:checked {
    background: #fff;
    border: 2px solid rgba(16, 108, 200, 0.87); }
    .option-input:checked::before {
      height: 20px;
      width: 20px;
      position: absolute;
      display: inline-block;
      font-size: 12px;
      line-height: 17px;
      content: "\f111";
      font-family: FontAwesome;
      font-style: normal;
      font-weight: normal;
      color: rgba(16, 108, 200, 0.87);
      margin-left: 3px; }
    .option-input:checked::after {
      -webkit-animation: click-wave 0.65s;
